import { Component, OnInit } from '@angular/core';
import { AppService } from '../app.service';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  constructor(private appService: AppService) { }

  launchData;
  apiUrl = 'https://api.spacexdata.com/v3/launches?limit=100';
  launchYear = '';
  launchStatus = '';
  landingStatus ='';
  ngOnInit(): void {
    // this.apiUrl = 'https://api.spacexdata.com/v3/launches?limit=100';
    this.fetchLaunchData(this.apiUrl);
  }

  filterLaunchYear(e){
    this.launchYear =  e.target.value;
    // this.apiUrl += this.launchYear;
    let url ='';
    // if(this.launchStatus){
      url = this.apiUrl + '&launch_success=' + this.launchStatus + '&launch_year=' + this.launchYear + 
      '&land_success=' + this.landingStatus;
    // } else{
    //   url = this.apiUrl + '&launch_year=' + this.launchYear;
    // }
    
    this.fetchLaunchData(url);
  }

  filterLaunchStatus(e){
    this.launchStatus =  e.target.value;
    // this.apiUrl += status;
    let url = '';
    // if(this.launchYear) {
      url = this.apiUrl + '&launch_success=' + this.launchStatus + '&launch_year=' + this.launchYear + 
      '&land_success=' + this.landingStatus;
    // }
    //  else{
    //   url = this.apiUrl + '&launch_success=' + this.launchStatus;
    // }
    
    this.fetchLaunchData(url);
  }

  filterLandingStatus(e){
    let url = '';
    this.landingStatus = e.target.value;
    // if(this.launchYear) {
      url = this.apiUrl + '&launch_success=' + this.launchStatus + '&launch_year=' + this.launchYear + 
      '&land_success=' + this.landingStatus;
    // let landing_status = '&land_success=' + e.target.value;
    // let url = 'https://api.spaceXdata.com/v3/launches?limit=100' + '&launch_success=true' + landing_status;
    this.fetchLaunchData(url);
  }

  fetchLaunchData(url){
    this.appService.makeHttpGet(url).subscribe(data=>{
      console.log(data);
      this.launchData = data;
    }, err=>{
      console.log(err);
    })
  }

}
